
export default {
    components: {
    },
    data() {
        return {
            input:{
               email:"",
               code:""
            },
            alive:false,
            canSend:true,
            send:"点击获取"


        }

    },
    mounted() {

    },
    methods: {


        sendCode(){
            const that = this;
            let time = 60;
            if(!this.canSend) return;
            this.t = setInterval(() => {
                that.canSend = false;
                time--;
                that.send = time + "s";
                if (time < 1) {
                    window.clearInterval(that.t);
                    that.send = "重新发送";
                    that.canSend = true;
                }
            }, 1000);
        },
        submit(){

        }
    }
}